.container_Content{
    background-color: white;
    width: 80%;
    height: auto;
    margin: auto;
    display: flex;
    border-radius: 20px;
    box-shadow: 0 10px 20px rgb(80, 78, 78);
   
}

@media only screen and (max-width:600px){
    .container_Content{
        flex-direction: column !important;
        width: 90%;
    }
    .item_container{
        flex-direction: column;
        justify-content: center;
    }
    .Menu_container{
        width: 90%;
    }
    .social_network_container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .tag_container{
        display: flex;
       
        align-items: center;
        justify-content: center;
    }
    .recent_posts{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .recent-item{
        display: flex;
        padding: 0.5rem 0;
    }
    .customLink{
        margin: 0 1rem;
  
    }
}

.left_content{
    flex: 2;
    padding: 0.5rem;

}
.right_content{
    flex: 1; 
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
}
.right_content > div{
    border-bottom: 1px solid rgb(163, 162, 162); 
}
.social_network_container{
    width: 100%;
    margin: 1rem;
}
.social_network_container > h3{
    margin: 1rem 0;
}
.tag_container {
    width: 100%;
    margin: 1rem ;
}
.tag_container > h3{
    margin: 1rem 0;
}
.tag_container a{
    display: inline-block;
    padding: 5px 6px 6px 3px;
    margin: 0 3px 8px 11px;
    background: #ddd;
    font-size: 1rem !important;
    line-height: 1;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #444;
    position: relative;
}

.item_container{
    width: 90%;
    height: 100%;
    box-shadow: 0 6px 6px rgb(95, 94, 94);
    display: flex;
    border-radius: 10px;
    margin: 1rem;
}

.wrap_img{
    width: 13rem;
    height: 100%;
}
.custom_Img{
    max-width: 100%;
    max-height: 100%;
}
.description{
    position: absolute;
    bottom: 1.5rem;
    color: rgb(133, 128, 128);
    font-size: small;
    text-transform: initial;
}
.item_title{
    margin: 2rem auto;
}

.recent_posts{
    width: 100%;
    margin: 1rem 0;
}
.recent_posts h3{
    margin-bottom: 1rem;
}

.customLink{
  
    color: rgb(83, 75, 75);
    text-transform: uppercase;
}
.customDate{
    color: rgb(116, 108, 108);
}