.Menu_container{
    display: flex;
    width: 80%;
    height: 8rem;
    box-shadow: 10px 10px 10px #293942;
    margin: 1rem auto;
    border-radius: 20px;
    justify-content: center;
}
.menuBar{
    display: flex;
    width: 45%;
   
   
}
.menuBar > div {
    margin-top:auto;
    margin-right:0;
    margin-bottom:auto;
    margin-left:2rem
}
.btn_MenuItem{
    border-radius: 15px;
    background-color: #e8b14b;
    padding: 1rem  2.5rem;
    text-decoration: none;
    color: white;
}
.searchBar{
    width: 50%;
    height: 50%;
    margin: auto;
}

.search-Value-Container{
    background-color: white;
    box-shadow: 0 0 4px;
    position: relative;
    z-index: 9999;
    
}
.search-Item {
    text-decoration: none;
    color: rgb(41, 39, 39);
    height: auto;
}
.search-Item h4{
    background-color: rgb(250, 249, 250);
    padding: 0.5rem;
    border-top: 1px solid;
}
.search-Item h4:hover{
    background-color: rgb(38, 70, 63);
    color: white;
}
@media only screen and (max-width:600px){
    .Menu_container{
        flex-direction: column;
        height: 9rem;
    }
    .searchBar{
      width: 90%;
      height: 30%;
    }
    .btn_MenuItem{
        padding: 1rem  1.5rem;
      
    }
    .menuBar{
        width: 100%;
    }
    .menuBar > div{
        margin: auto;
    }
}